<template>
  <div class="loginWrapper">
    <img class="login_bg" src="../assets/img/login.jpg">
    <div class="loginCardWrapper">
      <Card class="loginCard">
        <div class="logo">
          <router-link to="/home">
            <img src="../assets/img/logo.jpg">
          </router-link>
        </div>
        <div>
          <h2>了解世界,从妙漫开始</h2>
        </div>
        <!--登录-->
        <div class="login_page">
          <div>
            <h2 style="color: #333">{{font}}</h2>
            <h3 style="color: rgb(45, 140, 240);text-align: center"><a href="http://www.immnc.com/">如果没有跳转,请点击这里</a></h3>
          </div>
        </div>
      </Card>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {saveToLocal,loadURL} from "../../src/common/js/store.js"

  const PRIFIX = "https://news.immnc.com"

  export default {
    data() {
      return {
        inputType: "password",
        inputFlag: true,
        forgetPassword: true,
        flag: true,
        loginType: true,
        disabled: false,
        verifyFlag: false,
        time: 60,
        timeCode: "获取验证码",
        phone: "",
        password: "",
        password2: "",
        password3: "",
        password4: "",
        codeForget: "",
        codeRegister: "",
        font: '正在登陆请稍后...'
      }
    },
    created() {
      let id = this.$route.query.weChatOpenID
      var qs = require('qs')
      var self = this
      this.$http.post(PRIFIX + '/api/user/wechat/login', qs.stringify({
        'weChatOpenID': id
      })).then(function (response) {
        response = response.data
        if (response.code == 0) {
          let user = response.data.userVO
          saveToLocal(
            user.userId,
            user.nikeName,
            user.avatar,
            user.qq,
            user.sex,
            user.email,
            user.city,
            user.birthday,
            user.signature,
            user.backgroundImg,
            user.phone,
            user.fansCounts,
            user.followCounts,
            true
          )
          let uuu = loadURL()
          self.$Notice.success({
            title: '登陆成功',
          })
          if(uuu == '' || uuu == null) {
            setTimeout(function () {
              window.location.href = 'http://www.immnc.com/'
            },1000)
          } else {
            setTimeout(function () {
              window.location.href = 'http://www.immnc.com/' + uuu
            },1000)
          }
        }
      }).catch(function (error) {})
    }
  }

</script>

<style lang="less" scoped>
  .loginWrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    overflow: hidden;
  }

  .login_bg {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .loginCardWrapper {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /*排列方式 垂直*/
    -webkit-box-orient: vertical;
    /*是否反方向显示*/
    -webkit-box-direction: normal;
    /*排列方式 列*/
    -ms-flex-direction: column;
    flex-direction: column;
    /*对齐方式 居中 配合flex-pack使用上下左右居中对齐*/
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    /*对齐方式 居中*/
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    /*内容对齐 居中不留空隙*/
    justify-content: center;
    border-radius: 2px;
    min-height: 688px;
    /*calc()函数。这里的意思是设置宽度比100%的宽度少42px。*/
    height: calc(100% - 42px);
    /*并列显示*/
    box-sizing: border-box;
  }

  .loginCard {
    width: 432px;
  }

  .loginCard h2 {
    color: #ed4040;
    text-align: center;
    margin-top: 20px;
  }

  .logo {
    width: 244px;
    height: 54px;
    margin: 0 auto;
  }

  .logo img {
    width: 256px;
    height: 74px;
  }

  .logo img:hover {
    cursor: pointer;
  }

  .inputWrapper {
    margin-top: 30px;
  }

  .inputContent {
    position: relative;
    margin: 15px 0px;
  }

  .inputCss {
    border: none;
    padding: 5px;
    font-size: 16px;
    border-bottom: 1px #ebebeb solid;
    outline: none;
    width: 398px;
    height: 48px;
  }

  .inputIcon {
    position: absolute;
    right: 0px;
    top: 13px;
    font-size: 22px;
  }

  .inputHover:hover {
    cursor: pointer;
  }

  .inputButton {
    position: absolute;
    right: 0px;
    top: 8px;
  }

  .description {
    margin-top: 20px;
  }

  .phoneWay {
    display: inline-block;
  }

  .phoneWay a {
    color: #175199;;
  }

  .forget a {
    color: #8590a6;
  }

  .loginButton button {
    margin-top: 20px;
    width: 100%;
    font-size: 16px;
  }

  .otherLogin {
    margin-top: 15px;
  }

  .title {
    margin-bottom: 10px;
  }

  .other_login_icon {
    display: inline-block;
    margin-right: 30px;
    text-align: center;
  }

  .other_login_icon img {
    width: 40px;
    height: 40px;
  }

  .other_login_icon:hover {
    cursor: pointer;
    color: #ed3f14;
  }

  .register {
    position: relative;
    margin-top: 5px;
  }

  .rightWrapper {
    float: right;
  }

  .rightWrapper a {
    color: #2d8cf0;
  }

  .login {
    margin-top: 55px;
  }

</style>



